@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  src: local("Roboto bold"), url("../fonts/Roboto/Roboto-Bold.woff") format("woff"), url("../fonts/Roboto/Roboto-Bold.ttf") format("ttf");
}
@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  src: local("Roboto Medium"), url("../fonts/Roboto/Roboto-Medium.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-weight: 400;
  font-style: normal;
  src: local("Open Sans"), url("../fonts/OpenSans/OpenSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-weight: 600;
  font-style: normal;
  src: local("OpenSans Semibold"), url("../fonts/OpenSans/OpenSans-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-weight: 700;
  font-style: normal;
  src: local("OpenSans bold"), url("../fonts/OpenSans/OpenSans-Bold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 300;
  font-style: normal;
  src: local("Montserrat Light"), url("../fonts/Montserrat/Montserrat-Light.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: auto;
  margin: 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 18px;
  color: #777777;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.title {
  font-size: 50px;
  font-weight: 700;
  font-stretch: normal;
  font-style: normal;
  line-height: 50px;
  letter-spacing: 0.5px;
  text-align: center;
  color: #333333;
  margin: 0;
}
.container {
  margin: 0 auto;
  padding: 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 70px;
  }
}
@media (min-width: 1400px) {
  .container {
    width: 1600px;
  }
}
.bgr {
  background-color: #a165f4;
  height: 200px;
  width: 100%;
  margin-top: -100px;
  z-index: -1;
  position: relative;
}
.promo {
  background-image: url("../img/mainPhoto.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 30px;
  background-position: right;
}
@media (min-width: 768px) {
  .promo {
    background-position: top center;
    padding-top: 80px;
  }
}
.promo__container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .promo__container {
    flex-direction: row;
  }
}
.promo__part {
  flex: 0  0 50%;
}
.promo__main-block {
  margin: 50px 0 50px;
}
@media (min-width: 768px) {
  .promo__main-block {
    margin: 190px 0 220px;
  }
}
.promo__title {
  font-size: 50px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 700;
  line-height: 1.04;
  letter-spacing: normal;
  text-align: left;
  color: #090000;
  margin: 0;
}
@media (min-width: 768px) {
  .promo__title {
    font-size: 80px;
    max-width: 520px;
  }
}
@media (min-width: 1400px) {
  .promo__title {
    font-size: 120px;
  }
}
.promo__title-part {
  color: #ffffff;
}
.promo__text {
  font-size: 30px;
  font-weight: 500;
  font-family: "Roboto", "Arial", sans-serif;
  font-stretch: normal;
  font-style: normal;
  text-align: left;
  color: #fbf934;
  margin: 20px 5px 100px;
}
@media (min-width: 768px) {
  .promo__text {
    font-size: 50px;
  }
}
.promo__text-part {
  color: #fcfcfc;
}
.promo__button {
  padding: 20px 85px 20px 50px;
  border-radius: 40px;
  box-shadow: -1.3px 4.8px 23.8px 1.3px rgba(189, 227, 239, 0.75);
  background-color: #fcfcfc;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.3;
  text-align: left;
  color: #151515;
  display: inline-block;
  position: relative;
}
.promo__button::before {
  content: "";
  background-image: url("../img/icon-right-arrow-long.svg");
  background-repeat: no-repeat;
  width: 25px;
  height: 12px;
  right: 45px;
  bottom: 25px;
  display: block;
  position: absolute;
}
.promo__button:hover {
  box-shadow: -1.3px 4.8px 23.8px 1.3px #bde3ef;
  color: #a165f4;
}
.promo__header-button {
  font-weight: 600;
  height: 65px;
  box-shadow: -1.3px 4.8px 23.8px 1.3px rgba(189, 227, 239, 0.75);
  background-color: #ffffff;
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px 0;
}
@media (min-width: 768px) {
  .promo__header-button {
    width: 365px;
    margin: 0;
  }
}
.promo__header-collection,
.promo__header-search {
  color: #151515;
  line-height: 1.44;
  font-weight: 600;
  flex: 0 0 125px;
  text-align: center;
}
.promo__header-collection:hover,
.promo__header-search:hover {
  color: #a165f4;
}
@media (min-width: 768px) {
  .promo__header-collection,
  .promo__header-search {
    flex: 0 0 155px;
  }
}
.promo__header-search {
  border-right: 1px solid #e0e0e0;
  position: relative;
}
.promo__header-search::before {
  content: "";
  display: inline-block;
  position: absolute;
  background-image: url("../img/lens.png");
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  top: 0;
  left: 0;
}
.promo__header-collection {
  position: relative;
}
.promo__header-collection::after {
  content: "";
  position: absolute;
  display: inline-block;
  background-image: url("../img/angle-arrow-down.png");
  background-repeat: no-repeat;
  width: 18px;
  height: 10px;
  top: 8px;
  right: 0;
}
.promo__header-logo {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .promo__header-logo {
    margin: 0;
  }
}
.showcase {
  margin: 100px 0 0;
}
.showcase__container {
  padding: 100px 0 50px;
  box-shadow: -1.3px 4.8px 47.5px 2.5px rgba(237, 235, 253, 0.75);
  background-color: #ffffff;
}
@media (min-width: 1400px) {
  .showcase__container {
    width: 1380px;
  }
}
.showcase__nav-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-shadow: -1.3px 4.8px 47.5px 2.5px rgba(239, 239, 240, 0.75);
  background-color: #ffffff;
  border-radius: 50px;
  max-width: 1100px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .showcase__nav-list {
    flex-direction: row;
  }
}
.showcase__wrap {
  max-width: 1100px;
}
.showcase__nav-link {
  font-size: 15px;
  font-weight: 600;
  line-height: 1.67;
  letter-spacing: 0.15px;
  text-align: center;
  color: #333333;
}
@media (min-width: 768px) {
  .showcase__nav-link {
    margin: 0 20px;
  }
}
@media (min-width: 1400px) {
  .showcase__nav-link {
    margin: 0 45px;
  }
}
.showcase__nav {
  margin-top: 60px;
}
.showcase__nav-link--act {
  text-shadow: -2.3px 8.7px 5px rgba(213, 200, 247, 0.75);
  font-size: 20px;
  line-height: 1.25;
  letter-spacing: 0.2px;
  text-align: center;
  color: #b394fa;
}
.showcase__tab {
  display: none;
}
.showcase__tab--act {
  display: block;
}
.showcase__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.bags {
  position: relative;
}
.bags::after {
  content: "";
  width: 100px;
  height: 100px;
  display: inline-block;
  vertical-align: bottom;
  background-image: url("../img/spritesheet.png");
  background-repeat: no-repeat;
  background-position: -70px -5px;
  position: absolute;
  top: 350px;
  left: 40px;
  z-index: -1;
}
.bags__container-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 60px;
}
.bags__container {
  box-shadow: -1.3px 4.8px 47.5px 2.5px rgba(237, 235, 253, 0.75);
  background-color: #ffffff;
  padding: 100px 0 50px;
  margin-top: -40px;
  position: relative;
}
@media (min-width: 1400px) {
  .bags__container {
    width: 1380px;
  }
}
.bags__container::after {
  content: "";
  width: 60px;
  height: 60px;
  display: inline-block;
  vertical-align: bottom;
  background-image: url("../img/spritesheet.png");
  background-repeat: no-repeat;
  background-position: -197px -5px;
  position: absolute;
  top: 100px;
  left: 300px;
}
.bags__container::before {
  content: "";
  width: 50px;
  height: 110px;
  display: inline-block;
  vertical-align: bottom;
  background-image: url("../img/spritesheet.png");
  background-repeat: no-repeat;
  background-position: -5px, -110px;
  position: absolute;
  top: 0;
  right: 150px;
  left: auto;
}
.newsletter {
  background-image: linear-gradient(to top, #8c84f9, #a165f4);
  padding: 0 0 100px;
  position: relative;
}
@media (min-width: 1400px) {
  .newsletter::before {
    content: "get notified";
    display: block;
    position: absolute;
    font-size: 150px;
    font-weight: 900;
    text-align: center;
    color: rgba(254, 254, 254, 0.04);
    text-transform: uppercase;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
  }
}
.newsletter__title {
  font-size: 50px;
  line-height: 0.52;
  letter-spacing: 0.5px;
  text-align: center;
  color: #ffffff;
  display: block;
}
.newsletter__input {
  background-color: white;
  border: none;
  height: 70px;
  border-radius: 50px;
  font-size: 20px;
  padding: 27px;
  color: #333333;
  width: 100%;
}
@media (min-width: 1400px) {
  .newsletter__input {
    padding: 27px 200px 27px 27px;
  }
}
.newsletter__form {
  text-align: center;
  margin-top: 120px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (min-width: 1400px) {
  .newsletter__form {
    width: 850px;
  }
}
.newsletter__button {
  color: #8c84f9;
  border: none;
  background: #fffefe;
  font-size: 20px;
  padding: 15px 50px;
  border-radius: 50px;
  margin-top: 30px;
  display: inline-block;
}
.newsletter__button:hover {
  opacity: 0.9;
  box-shadow: -1px 4px 23px 1px #bde3ef;
}
@media (min-width: 1400px) {
  .newsletter__button {
    color: #fffefe;
    background: linear-gradient(to top, #8c84f9, #a165f4);
    margin-top: 0;
    position: absolute;
    top: 5.5px;
    right: 5.5px;
    padding: 18px 60px;
  }
}
.about-us__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 30px;
}
@media (min-width: 768px) {
  .about-us__container {
    flex-direction: row;
    margin-top: 0;
  }
}
.about-us__part {
  flex: 1 0 40%;
}
@media (min-width: 1400px) {
  .about-us__part {
    margin-left: 130px;
  }
}
.about-us__title {
  text-align: left;
}
.about-us__des {
  font-size: 25px;
  line-height: 1.36;
  letter-spacing: 0.25px;
  color: #333333;
}
.about-us__des-part {
  color: #8d83f9;
}
.about-us__text {
  font-size: 16px;
  line-height: 1.63;
  letter-spacing: 0.16px;
  color: #888888;
}
.social {
  margin-left: -20px;
  margin-right: -20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.social__item {
  fill: #555555;
  margin-left: 20px;
  width: 46px;
  height: 46px;
  border: solid 1px #ecebed;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social__item:hover {
  box-shadow: -1px 3.9px 9.5px 0.5px rgba(207, 207, 208, 0.75);
  border: solid 0.5px #ecebed;
  background: linear-gradient(to top, #8c84f9, #9b6df5);
  fill: #ffffff;
}
.footer {
  padding: 50px 0;
}
.footer__nav-list {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 40px 0;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer__nav-list {
    flex-direction: row;
  }
}
.footer__nav__link {
  color: #222222;
}
.footer__nav__link:hover {
  color: #a165f4;
}
.footer__nav-item {
  font-size: 18px;
  font-weight: 600;
  line-height: 1.44;
  color: #404040;
  margin: 0 25px;
}
.footer__logo {
  font-size: 40px;
  font-weight: 600;
  color: #222222;
  text-align: center;
  display: block;
}
.copyright {
  background-color: #f6f5f7;
  padding: 24px 0;
}
.copyright__text {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.73;
  text-align: center;
  color: #777777;
  margin: 0;
}
.product-cart {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 352px;
  margin: 10px;
  padding: 10px;
}
@media (min-width: 768px) {
  .product-cart {
    margin: 30px;
    padding: 20px;
  }
}
.product-cart--act {
  box-shadow: -1.3px 4.8px 47.5px 2.5px rgba(221, 223, 224, 0.75);
  background-color: #ffffff;
}
.product-cart__img {
  height: 260px;
}
.product-cart__price {
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 0.25px;
  text-align: center;
  color: #333333;
}
.product-cart__price--act {
  color: #8c84f9;
}
.product-cart__activity {
  opacity: 0;
  visibility: hidden;
  font-size: 14px;
  line-height: 1.79;
  letter-spacing: 0.14px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: #bbbbbb;
}
.product-cart__activity--act {
  opacity: 1;
  visibility: visible;
  transition: 0.55s opacity, 0.55s visibility;
}
.product-cart__like::before {
  content: "";
  background-image: url("../img/like.png");
  background-repeat: no-repeat;
  width: 19px;
  height: 17px;
  display: inline-block;
  background-position: center;
  margin-right: 7px;
}
.product-cart__review {
  text-align: right;
  display: flex;
  align-items: center;
}
.product-cart__review::before {
  content: "";
  background-image: url("../img/comment.png");
  background-repeat: no-repeat;
  width: 25px;
  height: 21px;
  display: inline-block;
  background-position: center;
  margin-right: 7px;
}
.product-cart__button {
  box-shadow: -1.3px 4.8px 23.8px 1.3px rgba(240, 239, 254, 0.75);
  background: linear-gradient(to top, #8c84f9, #a165f4);
  padding: 20px 40px;
  font-size: 20px;
  color: #ffffff;
  line-height: 1.3;
  border-radius: 40px;
  display: block;
  margin: 0 auto -50px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s opacity, 0.55s visibility;
}
.product-cart__button--act {
  opacity: 1;
  visibility: visible;
}
